.collapse {
  overflow: hidden;
  transition: height 300ms cubic-bezier(0.49, 0.025, 0.49, 1);
}
.collapse:not(.active) {
  height: 0;
}

[data-collapse] .collapse-arrow,
[data-toggler] .collapse-arrow {
  transition: 300ms all cubic-bezier(0.49, 0.025, 0.49, 1);
}
[data-collapse].active .collapse-arrow,
[data-toggler].active .collapse-arrow {
  transform: rotate(-180deg);
}

.link-effect {
  position: relative;
  white-space: nowrap;
}
.link-effect::before, .link-effect::after {
  bottom: 2px;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  will-change: transform;
  left: 0;
  background-color: currentColor;
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.link-effect::before {
  opacity: 0;
  transform-origin: left;
}
.link-effect::after {
  opacity: 1;
  transform-origin: right;
}
.link-effect:hover::before, .link-effect:hover::after {
  transform: scaleX(1);
}
.link-effect:hover::before {
  opacity: 1;
}
.link-effect:hover::after {
  opacity: 0;
}

.animate-up {
  transform: translateY(15px);
  opacity: 0;
  transition: opacity 0.65s cubic-bezier(0.42, 0, 0.002, 1), transform 0.65s cubic-bezier(0.42, 0, 0.002, 1);
}

.highlight {
  position: relative;
}
.highlight::after {
  background-color: #FB3B45;
  content: "";
  position: absolute;
  bottom: 5px;
  height: 9px;
  width: 100%;
  left: 0;
  z-index: -1;
}

.zoom-effect > .zoom-effect-image {
  overflow: hidden;
}
.zoom-effect > .zoom-effect-image > img {
  transition: 0.6s transform ease;
}
.zoom-effect:hover > .zoom-effect-image > img {
  transform: scale(1.1);
}

.btn {
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
  border: none;
  transition: 300ms background-color cubic-bezier(0.49, 0.025, 0.49, 1), 300ms color cubic-bezier(0.49, 0.025, 0.49, 1), 300ms border-color cubic-bezier(0.49, 0.025, 0.49, 1), 300ms opacity cubic-bezier(0.49, 0.025, 0.49, 1), 300ms box-shadow cubic-bezier(0.49, 0.025, 0.49, 1), 300ms stroke cubic-bezier(0.49, 0.025, 0.49, 1), 300ms fill cubic-bezier(0.49, 0.025, 0.49, 1);
  display: flex;
  align-items: center;
  white-space: nowrap;
  justify-content: center;
  flex-shrink: 0;
}
.btn svg {
  flex-shrink: 0;
}
.btn svg * {
  transition: 300ms background-color cubic-bezier(0.49, 0.025, 0.49, 1), 300ms color cubic-bezier(0.49, 0.025, 0.49, 1), 300ms border-color cubic-bezier(0.49, 0.025, 0.49, 1), 300ms opacity cubic-bezier(0.49, 0.025, 0.49, 1), 300ms box-shadow cubic-bezier(0.49, 0.025, 0.49, 1), 300ms stroke cubic-bezier(0.49, 0.025, 0.49, 1), 300ms fill cubic-bezier(0.49, 0.025, 0.49, 1);
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn:active {
  transform: scale(0.95);
}

